@import './variables.css';

.profile {
  padding: var(--spacing-lg); /* Use spacing variable */
  background-color: var(--background-color); /* Use dark background */
  border-radius: var(--border-radius-md); /* Use border radius variable */
  color: var(--text-color); /* Light text color */
}

.profile__info {
  margin-bottom: var(--spacing-lg); /* Use spacing variable */
}

.editButton {
  background-color: var(--primary-color); /* Gold accent for edit button */
  color: white;
  border: none;
  padding: var(--spacing-sm) var(--spacing-md); /* Use spacing variables */
  border-radius: var(--border-radius-sm); /* Use border radius variable */
  cursor: pointer;
  transition: var(--transition); /* Add transition for hover effect */
}

.editButton:hover {
  background-color: #0056b3; /* Adjusted hover color */
}
