:root {
  /* Color Palette */
  --primary-color: #ffc107; /* Gold accent */
  --secondary-color: #6c757d; /* Example grey */
  --background-color: #121212; /* Dark background */
  --background-color-light: #2c2c2c;
  --text-color: #ffffff; /* Light text */
  --success-color: #28a745;
  --warning-color: #ffc107;
  --error-color: #dc3545;

  /* Spacing */
  --spacing-xs: 4px;
  --spacing-sm: 8px;
  --spacing-md: 16px;
  --spacing-lg: 24px;
  --spacing-xl: 32px;

  /* Border Radius */
  --border-radius-sm: 4px;
  --border-radius-md: 8px;
  --border-radius-lg: 16px;

  /* Shadows */
  --box-shadow: 0 4px 6px rgba(0, 0, 0, 0.5);

  /* Gradients */
  --gradient-primary: linear-gradient(45deg, #ffc107, #6610f2);
  --gradient-secondary: linear-gradient(45deg, #6c757d, #adb5bd);

  /* Typography */
  --font-size-sm: 12px;
  --font-size-md: 16px;
  --font-size-lg: 20px;
  --font-size-xl: 24px;

  /* Transition */
  --transition: all 0.3s ease;
}
