/* src/styles/login.module.css */

.loginContainer {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
  background-color: var(--background-color); /* Use your variable for dark background */
  color: var(--text-color); /* Light text color */
  padding: var(--spacing-md);
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
}

.loginTitle {
  font-size: var(--font-size-xl);
  margin-bottom: var(--spacing-lg);
  color: var(--primary-color); /* Accent color */
  font-weight: 600;
  text-align: center;
}

.loginForm {
  background-color: rgba(255, 255, 255, 0.1); /* Transparent white */
  padding: var(--spacing-lg);
  border-radius: var(--border-radius-lg);
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.1);
  backdrop-filter: blur(10px); /* Slight blur for a glass effect */
}

.formGroup {
  margin-bottom: var(--spacing-md);
}

.input {
  width: 100%;
  padding: var(--spacing-sm);
  border: 1px solid transparent; /* Start with transparent border */
  border-bottom: 2px solid var(--primary-color); /* Bottom border in accent color */
  border-radius: 0; /* No rounding for inputs */
  background: transparent; /* Transparent background */
  color: var(--text-color); /* Text color */
  font-size: var(--font-size-md);
  transition: border-color 0.3s;
  outline: none; /* Remove default outline */
}

.input::placeholder {
  color: var(--secondary-color); /* Subtle placeholder color */
}

.input:focus {
  border-color: var(--gold); /* Change to gold on focus */
}

.loginButton {
  width: 100%;
  padding: var(--spacing-sm);
  border: none;
  border-radius: var(--border-radius-md);
  background-color: var(--primary-color); /* Use accent color */
  color: white;
  font-size: var(--font-size-md);
  cursor: pointer;
  transition: background-color 0.3s, transform 0.2s;
  font-weight: 500;
}

.loginButton:hover {
  background-color: #0056b3; /* Darker shade on hover */
  transform: translateY(-2px); /* Subtle lift effect */
}

.loginButton:active {
  transform: translateY(1px); /* Pressed effect */
}
