@import './variables.css';

.navbar {
  display: flex;
  justify-content: space-between;
  background-color: var(--background-color); /* Changed to dark background */
  padding: var(--spacing-md);
  color: var(--text-color); /* Use light text color */
}

.navbar__logo a {
  color: var(--primary-color); /* Gold accent for logo */
  font-size: var(--font-size-lg);
  text-decoration: none;
}

.navbar__links {
  display: flex;
  list-style: none;
}

.navbar__links li {
  margin-left: var(--spacing-lg);
}

.navbar__links a {
  color: var(--text-color); /* Use light text color */
  text-decoration: none;
  transition: var(--transition);
}

.navbar__auth a {
  color: var(--text-color); /* Use light text color */
  text-decoration: none;
  margin-left: var(--spacing-lg);
}

.active {
  font-weight: bold;
  border-bottom: 2px solid var(--primary-color); /* Gold accent for active link */
}

.logoutButton {
  background-color: transparent; /* Change as needed */
  border: none;
  color: var(--primary-color); /* Gold accent for logout button */
  cursor: pointer;
  font-size: var(--font-size-md); /* Use variable for consistency */
}
